@import url(https://fonts.googleapis.com/css?family=Lato:300i,400,700);
body {
  margin: 0;
  padding: 0;
  background: #fff;
  font-family: 'Lato', sans-serif;
  font-size: 1.2em;
}

.logo {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}
.logo > .name {
  font-size: 6em;
  color: #363636;
}
@media (max-width: 960px) {
  .logo > .name {
    font-size: 12vw;
  }
}
.logo > .image {
  width: 20vw;
  max-width: 200px;
}
.no-bottom {
  padding-bottom: 0;
  margin-bottom: 0;
}
.no-top {
  padding-top: 0;
  margin-top: 0;
}
.big {
  font-size: 4em;
}

.Demo {
  padding-top: 60px;
  flex: 1 0 auto;
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
}

.Page {
  min-height: 100vh;
  box-sizing: border-box;
  padding: 2em calc(50vw - 960px / 2 + 2em);
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

@media (max-width: 960px) {
  .Page {
    padding: 2em;
  }
}

.Page:first-child {
  margin-bottom: -60px;
  padding-bottom: calc(60px + 10vh);
}

.Page:nth-child(1) {
  background-color: #71c9ec;
}
.Page:nth-child(3) {
  background-color: #7aaa62;
}
.Page:nth-child(4) {
  background-color: #ffce64;
}
.Page:nth-child(5) {
  background-color: #ff62ff;
}
.Page:nth-child(6) {
  background-color: #62dded;
}
.Page:nth-child(7) {
  background-color: #6262ff;
}
.Page:nth-child(8) {
  background-color: #7acbc5;
}
.Page:nth-child(9) {
  background-color: #ffb862;
}
.Page:nth-child(10) {
  background-color: #fd6e63;
}

.Page.tidslinje {
  background-color: #71c9ec;
  background-image: url(/timeline-person.png);
  background-position: center 60%;
  background-size: 180px;
}
.Page.tidslinje-2 {
  background-color: #fd6e63;
  background-image: url(/timeline-joystick.png), url(/timeline-drone.png);
  background-position: center 45%, center 90%;
  background-size: 130px, 180px;
  background-blend-mode: darken;
}
.Page.tidslinje-3 {
  background-color: #ffce64;
  background-image: url(/timeline-nesespray.png);
  background-position: center 80%;
  background-size: 240px;
}
.Page.tidslinje-4 {
  background-color: #7acbc5;
  background-image: url(/timeline-hospital.png), url(/timeline-house.png);
  background-position: calc(50% - 240px) 60%, calc(50% + 240px) 90%;
  background-size: 180px;
}

@media (max-width: 960px) {
  .Page.tidslinje-4 {
    background-position: 25% 50%, 75% 90%;
  }
  .Page {
    background-blend-mode: multiply;
  }
}

.Tabs {
  display: flex;
  flex-direction: row;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  bottom: 0;
  overflow-x: auto;
}

.Tabs > a {
  display: flex;
  padding: 0 1em;
  flex: 1 0 auto;
  height: 60px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.3);
  text-decoration: none;
  color: #fff;
}

.Tabs > .selected {
  background-color: rgba(0, 0, 0, 0.6);
}

.FadeInBlock {
  max-width: 320px;
  margin: 16px;
  flex: 1 0 auto;
}

.FadeInBlock:first-of-type {
  margin-top: 4em;
}

.FadeInBlock > h3,
.FadeInBlock > p {
  margin: 0;
  padding: 0;
}

.FadeInBlock[side='left'] {
  align-self: flex-start;
}

.FadeInBlock[side='right'] {
  align-self: flex-end;
}

